import React from 'react'
import Aboutme from '../img/aboutme.png'

function About() {
  return (
    <section id='about' style={{padding: '113px 0px '}}>
    <div className='container'> 
      <div className='row'> 
      <div className='col-md-6 align-self-center'> 
        <img src={Aboutme} className="my-image" alt="..." />

        </div>
        <div className='col-md-6'> 
        <h4>Hellow! My name is Jason, and I enjoy writing simple and effective code for my clients. My job is to debug website problems and create a better plan and code to make the site as fast and user friendly as possible. I enjoy planning and observing other coders to learn from them. My point of view is to do it correctly, whether it is a small or large task, so my interest in coding is very strong, and I want to explore and learn more about coding. My goal is to become a senior developer in the future and to create a useful application for users to enjoy.</h4>
        </div>
      </div>
    </div>
    </section>
  )
}

export default About