import React from 'react'
import WUL from '../img/WUL.png'
import TTRF from '../img/TTRF.png'
import SMNFT from '../img/SMNFT.png'
import WWN from '../img/WWN.png'



function RecentProjects() {

    var sectionStyle = {
        backgroundImage: "url(" + SMNFT + ")",
  backgroundPosition: '0px 160px',
  backgroundSize: 'contain',
  backgroundRepeat: 'no-repeat',
      };

      var sectionStyle1 = {
        backgroundImage: "url(" + TTRF + ")",
  backgroundPosition: '0px 160px',
  backgroundSize: 'contain',
  backgroundRepeat: 'no-repeat'
      };

     
      var sectionStyle2 = {
        backgroundImage: "url(" + WUL + ")",
  backgroundPosition: '0px 160px',
  backgroundSize: 'contain',
  backgroundRepeat: 'no-repeat'
      };

           
      var sectionStyle3 = {
        backgroundImage: "url(" + WWN + ")",
  backgroundPosition: '0px 160px',
  backgroundSize: 'contain',
  backgroundRepeat: 'no-repeat'
      };

  return (
    <div className='container'>
        <div className='row'>
            <div className='col-md-12 text-center pt-5'>
            <h1>
                Recent Projects
            </h1>
            </div>
            <div className='col-xxl-6 col-xl-6 col-lg-6 pt-5'>
            <div className="card">
                <div className="card-content" style={sectionStyle}>
                    <div className="front-view">
                    <h3 className="title">Block Chain Music NFT</h3>
                    </div>
                    <div className="back-view ">
                        <div className="back-para">
                        A Block Chain website for music NFT
                        </div>
                        <div className="back-para">
                        <a rel="noreferrer"  className='text-decoration-none' href='https://seemynft.page/'
                        target="_blank" >
                            <button id="neonShadow" className="btn ">
                            <span>  
                                 Download CV
                            </span>
                            </button>
                        </a>
                        </div>
                    </div>
                </div>
            </div>
            </div>
            <div className='col-xxl-6 col-xl-6 col-lg-6 pt-5'>
            <div className="card">
                <div className="card-content" style={sectionStyle1}>
                    <div className="front-view">
                    <h3 className="title">Swedish Yearly Festivals </h3>
                    </div>
                    <div className="back-view">
                        <div className="back-para">
                            A website for Swedish Festival
                        </div>
                        <div className="back-para">
                        <a   className='text-decoration-none' href='https://timetorockfestival.se/' target="_blank" rel="noreferrer">
                            <button id="neonShadow" className="btn ">
                            <span>  
                                 Download CV
                            </span>
                            </button>
                        </a>
                   
                        </div>
                    </div>
                </div>
            </div>
            </div>
            <div className='col-xxl-6 col-xl-6 col-lg-6 py-5'>
                <div className="card">
                    <div className="card-content" style={sectionStyle2}>
                        <div className="front-view">
                        <h3 className="title">Raise Support for Ukraine </h3>
                        </div>
                        <div className="back-view">
                            <div className="back-para">
                            A website to Raise support for Ukraine 
                            </div>
                            <div className="back-para">
                          
                            <a  className='text-decoration-none' href='https://worldunited.live/'
                        target="_blank" rel="noreferrer" >
                            <button id="neonShadow" className="btn ">
                            <span>  
                                 Download CV
                            </span>
                            </button>
                        </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className='col-xxl-6 col-xl-6 col-lg-6 py-5'>
                <div className="card">
                    <div className="card-content" style={sectionStyle3}>
                        <div className="front-view">
                        <h3 className="title">Plastic Surgery </h3>
                        </div>
                        <div className="back-view">
                            <div className="back-para">
                            A website for Plastic Surgery ,Breast Augmentation , Rhinoplasty etc.
                            </div>
                            <div className="back-para">
                            <a className='text-decoration-none' href='https://optimalbeauty.com/'
                                 target="_blank" rel="noreferrer" >
                                <button id="neonShadow" className="btn ">
                                <span>  
                                    Download CV
                                </span>
                                </button>
                             </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
  )
}

export default RecentProjects