import React from 'react'
import Parallax from '../img/pexels-pixabay-35888.jpg'

function Imageparallax() {
  return (
        <div className='container-fluid'>
        <div className='row'>
            <div id='parallaxIMG' className='col-md-12 p-0' style={{backgroundImage: `url(${Parallax})`}}>      
            </div>
        </div>
        </div>
  )
}

export default Imageparallax