import React from 'react'
import Me from '../img/me.png'
import Imageparallax from "./Imageparallax";

function Introduction() {
    var TxtType = function(el, toRotate, period) {
        this.toRotate = toRotate;
        this.el = el;
        this.loopNum = 0;
        this.period = parseInt(period, 10) || 2000;
        this.txt = '';
        this.tick();
        this.isDeleting = false;
    };

    TxtType.prototype.tick = function() {
        var i = this.loopNum % this.toRotate.length;
        var fullTxt = this.toRotate[i];

        if (this.isDeleting) {
        this.txt = fullTxt.substring(0, this.txt.length - 1);
        } else {
        this.txt = fullTxt.substring(0, this.txt.length + 1);
        }

        this.el.innerHTML = '<span class="wrap">'+this.txt+'</span>';

        var that = this;
        var delta = 200 - Math.random() * 100;

        if (this.isDeleting) { delta /= 2; }

        if (!this.isDeleting && this.txt === fullTxt) {
        delta = this.period;
        this.isDeleting = true;
        } else if (this.isDeleting && this.txt === '') {
        this.isDeleting = false;
        this.loopNum++;
        delta = 500;
        }

        setTimeout(function() {
        that.tick();
        }, delta);
    };

    window.onload = function() {
        var elements = document.getElementsByClassName('typewrite');
        for (var i=0; i<elements.length; i++) {
            var toRotate = elements[i].getAttribute('data-type');
            var period = elements[i].getAttribute('data-period');
            if (toRotate) {
              new TxtType(elements[i], JSON.parse(toRotate), period);
            }
        }
        // INJECT CSS
        var css = document.createElement("style");
        css.type = "text/css";
        css.innerHTML = ".typewrite > .wrap { border-right: 0.08em solid #fff}";
        document.body.appendChild(css);
    };
  return (
<section id='introduction'>
    <div className='container-fluid'>
        <div className='row'>
            <div className='col-md-6 text-end'>
            <img src={Me} className="my-image" alt="..." />
            </div>
            <div className='col-md-6'>
                <h1 className='intro-text'>
                    <a className="typewrite text-white text-decoration-none" data-period="1500" data-type='[ "Hi, Im Jason.", "I am Creative.", "I Love Design.", "I Love to Develop." ]'>
                        <span className="wrap"></span>
                    </a>
                </h1>
                <h3 className='pe-5'>I'm a Web Developer from Phillipines, I have 1 year Experience as a web developer I love to create and recreate design for my clients expectation</h3>
            </div>
        </div>
    </div>
    <div className='container-fluid'>
        <div className='row'>
        <Imageparallax/>
        </div>
    </div>

</section>

  )
}

export default Introduction