import React from 'react'
import CVPDF from '../img/Myresume.pdf'
import ClickButton from '../img/clickbutton.png'

function CVDownload() {
  return (
    <div className='container'>
        <div className='row'>
            <div className='col-md-6 text-center align-self-center'>
            <a className='text-decoration-none' rel="noreferrer" href={CVPDF} target="_blank" >
                <button id="neonShadow" className="btn ">
                <span>  
                Download CV
                </span>
                </button>
            </a>
            </div>
            <div className='col-md-6 '>
            <img src={ClickButton} className="my-image" alt="..." />
            </div>
        </div>
    </div>
  )
}

export default CVDownload