import React from 'react'
import Introduction from "./Introduction";
import About from "./About";
import Skills from "./Skills";
import CVDownload from "./CVDownload";
import Scrollspy from 'react-scrollspy'
import RecentProjects from "./RecentProjects";

function Navbar() {
  return (
<>

<nav className="sidebar navbar-expand-lg navbar-light bg-light">
  <div className="">
    <h1 className="navbar-brand text-center py-4 m-0 fs-1 fw-bold">Jason <br/> Calayca</h1>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarTogglerDemo02" aria-controls="navbarTogglerDemo02" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse justify-content-center" id="navbarTogglerDemo02">
     <Scrollspy 
        className="scrollspy p-0 text-center" items={ ['introduction', 'about', 'skills', 'recent-Projects'] } 
        currentClassName="active">
        <li><a href="#introduction">Introduction</a></li>
        <li><a href="#about">About</a></li>
        <li><a href="#skills">Skills</a></li>
        <li><a href="#recent-Projects">Recent Projects</a></li>
      </Scrollspy>
    </div>
  </div>
</nav>


 <div className="allcontent bg-dark text-white">
      <div>
        <section id="introduction">
        <Introduction/>
          </section>
        <section id="about" >
          <h2><About/></h2>          
          </section>
          <hr/>
          <section >
          <CVDownload/>
          </section>
          <hr/>
        <section id="skills">
        <Skills/>
          </section>
          <hr/>
        <section id="recent-Projects">
        <RecentProjects/>
        </section>
      </div>
    </div>
</>
  )
}

export default Navbar